
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

.login-wrap,.register-wrap{text-align:center
}
input{display:block;width:250px;height:40px;line-height:40px;margin:0 auto;margin-bottom:10px;outline:none;border:1px solid #888;padding:10px;-webkit-box-sizing:border-box;box-sizing:border-box;
}
p{color:red;
}
button{display:block;width:250px;height:40px;line-height:40px;margin:0 auto;border:none;background-color:#41b883;color:#fff; font-size:16px;margin-bottom:5px
}
span{cursor:pointer;
}
span:hover{color:#41b883;
}

ul{ padding:0; text-align:center;/* border:solid 1px red;*/
}
ul li{list-style: none;
}
